
* {
  margin: 0 auto;
  padding: 0 auto;
}

.App {
  text-align: start;
  font-family: 'Jost';
  text-align: center;
}


/* HEADER */

nav {
  background-color: #A6C48A;
}

.navbar {
  display: grid;
  grid-template-columns: 10% 60% 10% 10% 10%;
  padding: 0.8% 0;
  justify-items: center;
}

.homelogo {
  white-space: nowrap;
  align-self: center;
}


a {
  text-decoration: none;
  color: rgb(251, 243, 243);
}

.nav_item_mission {
  grid-column: 3;
  align-self: center;
}

.button_mission {
  font-size: 100%;
  border-radius: 10%;
  border: #A6C48A solid 1px;
  font-family: 'Jost';
  font-weight: 700;
  color: rgb(74, 72, 72);
  background-color: rgb(251, 243, 243);
}

.button_store {
  font-size: 100%;
  border-radius: 10%;
  border: #A6C48A solid 1px;
  font-family: 'Jost';
  font-weight: 700;
  color: rgb(74, 72, 72);
  background-color: rgb(251, 243, 243);
}

.button_mission:hover {
  background-color: rgb(251, 228, 170);
}

.button_store:hover {
  background-color: rgb(251, 228, 170);
}

.nav_item_store {
  grid-column: 4;
  white-space: nowrap;
  align-self: center;
}

.cart_counter {
color: rgb(251, 243, 243);
grid-column: 5;
align-self: center;
display: flex;
}

#cart_icon {
  width: 3vw;
}

.counter_text {
  font-size: 140%;
  font-weight: 500;
  align-self: center;
  padding-left: 12%;
}


/* HEADER CLOSE */

/* HOME */

.homeMain {
  background-image: url('./views/images/Wildlife-2.jpeg');
  background-size: cover;
}

#home_intro_text {
  font-size: 170%;
  font-weight: 400;
  color:  rgb(251, 243, 243);
  padding: 5% 20%;
}

.homelogo {
  font-size: 170%;
  margin-left: 60%;
}

.homeCollectionImg {
  width: 70%; /* Using percentage for responsiveness */
  padding: 3% 0 5% 0;
  overflow: hidden;
  transition: transform .5s ease;
}

.homeCollectionBg {
  overflow: hidden;
}

.homeCollectionBg:hover .homeCollectionImg {
  transform: scale(1.1);
}

.font2 {
  font-family: Abel;
  padding: 2% 0;
  width: 60%;
}

/* HOME CLOSE */

/* MISSION */

.mission_title {
  font-family: 'Jost';
  padding: 1% 0%;
  font-size: 250%;
}

.mission_intro {
  padding: 3% 5%;
  color: rgb(62, 62, 62);
  background-color: rgb(250, 238, 199);
  margin: 3% 15%;
  box-shadow: 0 0 50px rgb(228, 228, 228);
}

.mission_intro_text {
  font-family: 'Abel';
  font-size: 100%;
}


.mission_amazon_charities {
  display:flex;
  flex-direction: row;
  justify-content: end;
  font-family: 'Abel';
  padding: 10% 0;
  font-size: 100%;
  height: 37vw;
  padding: 0 0;
}

.mission_wildlife_charities {
  font-size: 100%;
  display: flex;
  justify-content: end;
  height: 38vw;
}

.mission_ocean_charities {
  display: flex;
  justify-content: end;
  height: 38vw;
  font-size: 100%;
}

.amazon_charity_text {
  font-size: 100%;
  padding: 4% 4%;
  margin-right: 2%;
}

.wildlife_charity_text {
  font-size: 100%;
  padding: 4% 4%;
  font-family: 'Abel';
}

.ocean_charity_text {
  font-size: 100%;
  font-family: 'Abel';
  padding: 4% 2%;
  margin-right: 2%;
}

.amazon_mission_title {
  font-size: 120%;
  font-family: 'Jost';
  padding-bottom: 5%;
  text-decoration: underline;
  color: #A6C48A;
  font-weight: 700;
}

.ocean_mission_title {
  font-family: 'Jost';
  font-size: 120%;
  padding-bottom: 5%;
  color: rgb(19, 112, 119);
  text-decoration:underline;
}

.wildlife_mission_title {
  font-size: 120%;
  font-family: 'Jost';
  padding-bottom: 5%;
  text-decoration: underline;
  color: rgb(66, 127, 46);
}



.amazon_webpage_link {
  padding: 2% 4%;
  font-size: 80%;
  margin-top: 7%;
  font-family: 'Jost';
  background-color: #A6C48A;
}

.amazon_webpage_link:hover {
  background-color: rgb(205, 252, 190);
}

.wildlife_webpage_link {
  padding: 2% 4%;
  font-size: 80%;
  margin-top: 7%;
  font-family: 'Jost';
  background-color: rgb(66, 127, 46);
  color: rgb(254, 229, 229);
}

.wildlife_webpage_link:hover {
  background-color: rgb(26, 54, 14);
}

.ocean_webpage_link {
  padding: 2% 4%;
  font-size: 80%;
  margin-top: 7%;
  font-family: 'Jost';
  background-color: rgb(19, 112, 119);
  color: rgb(254, 229, 229);
}

.ocean_webpage_link:hover {
  background-color: rgb(6, 18, 47);
}


/* MISSION CLOSE */

/* STORE */

.store_header {
  padding: 1.5% 0%;
  background-color: rgb(249, 249, 228);
  font-size: 120%;
  font-family: 'Jost';
  font-weight:700;
  color: rgb(74, 73, 73);
}

#collection {
  font-size: 80%;
  border: 1px solid #A6C48A;
  margin: 0% 1.5%;
  color: rgb(74, 73, 73);
  font-family: 'Jost';
}

#category {
  font-size: 80%;
  border: 1px solid #A6C48A;
  margin: 0% 1.5%;
  color: rgb(74, 73, 73);
  font-family: 'Jost';
}

#price {
  font-size: 80%;
  border: 1px solid #A6C48A;
  margin: 0% 1.5%;
  color: rgb(74, 73, 73);
  font-family: 'Jost';
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20%, 1fr));
  gap: 5%;
  margin: 2% 2%;
  max-height: calc(100vh - (2vw + 3vw + 1vw)) ;
  overflow-y: auto; /* Add a vertical scrollbar when needed */
}

.product_mainwrap {
  border: 0.5px solid rgb(252, 165, 181);
  border-radius: 3%;
  box-shadow: 0 0 15px rgb(255, 238, 241);
  padding: 4%;
  display: flex;
  flex-direction: column;
}

.productInfo {
  display: flex;
  flex-direction: column;
  font-size: 80%;
}

.productImages {
  max-width: 100%;
  height: 20vw;
  margin-bottom: 5%;
}

.productImages:hover {
  opacity: 50%;
}

.productNames {
  font-size: 130%;
  font-weight: 400;
  margin-bottom: 4%;
}


/* STORE CLOSE */


/* SINGLE PRODUCT PAGE */

.singleProductImg {
  width: 30vw;
  margin-bottom: 2%;
}

.singleProductTitle {
  padding: 3% 0;
  font-size: 150%;
}

.singleProductInfo {
  border: 1px solid pink;
  border-radius: 2%;
  margin: 3% 20%;
  color: rgb(59, 59, 59);
}

.singleProductDetails {
  font-size: 80%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  row-gap: 1vw;
  width: 30vw;
}

.singleProductDetail {
  border: 1px solid #A6C48A;
  border-radius: 4%;
  padding: 1% 2%;
}

.cartButtons1 {
margin: 2% 1%;
font-family: 'Jost';
font-size: 100%;
border: 2px solid #A6C48A;
border-radius: 5%;
background-color: #A6C48A;
font-weight: 700;
padding: 1% 2%;
}

.cartButtons2 {
  margin: 5% 1%;
  font-family: 'Jost';
  font-size: 100%;
  border: 2px solid #A6C48A;
  border-radius: 5%;
  background-color: #A6C48A;
  font-weight: 700;
  padding: 1% 2%;
}

/* SINGLE PRODUCT PAGE CLOSE */

/* CART */

.cart_title {
  padding: 5% 0 3% 0;
}

.cart_wrapper {
  display: flex;
  flex-direction: row;
}

.cart_items_left {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-left: 3%;
}

.cart_item {
  width: 100%;
  display: flex;
  margin-bottom: 2%;
  border: solid 1px pink;
}

.item_content {
  width: 100%;
  display: flex;
  align-items: center;
}

.image_wrapper {
  flex: 0 0 15%;
  margin-right: 2px;
}

.image_wrapper img {
  width: 100%;
  height: auto;
}

.info_wrapper {
  flex: 1; 
  display: flex;
  justify-content: space-between;
}

.quantity_buttons {
  display: flex;
  align-items: center;
}

.cart_right_panel {
  flex: 0 0 30%; 
}

.cart_counter {
  align-self: flex-end;
  margin-top: 20px;
}

.right_content {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.cart_total_text {
  color: grey;
  border: 0.5px pink solid;
  padding: 3% 5%;
  font-size: 120%;
}

.order_button {
  font-size: 120%;
  margin: 0 20%;
  font-family: 'Jost';
  border: 1px pink solid;
  border-radius: 10%;
}

.order_button:hover {
  background: rgb(249, 221, 171);
}

.total_amount {
  color: rgb(12, 56, 26);
}

/* CART CLOSES */


/* FOOTER */

footer {
  background: #A6C48A;
  padding: 2% 0;
  color: white;
}

.rights_reserved_text {
  margin-bottom: 1.2%;
  font-family: 'Jost';
  font-weight: 500;
  font-size: 110%;
}

.fb_logo {
  width: 2%;
  margin: 0 0.5%;
}

.insta_logo {
  width: 2%;
  margin: 0 0.5%;
}

/* FOOTER CLOSE */





